/* @import url("https://fonts.googleapis.com/css2?family=DynaPuff:wght@200&display=swap"); */

* {
  /* color: #9be8a1; */
  font-family: "DynaPuff", cursive;
  border: 1px solid white;
  /* font-weight: 100; */
}

* .Paper {
  border: 1px solid white;
}
:root {
  --primary: #25092c;
  --secondary: #9be8a1;
  --lovelypink: #fe8daa;
  --binance_black: #0b0e11ff;
  --binance_ash: #111518ff;
  --binance_yellow: #fcd535ff;
  --binance_white: #eaecefff;
  --binance_green: #14cb85ff;
  --binance_brightash: #6b7f96ff;
}
h1 {
  color: var(--secondary);

  font-family: "DynaPuff", cursive;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.5s ease;
}

h1:hover {
  transform: translateX(-20%);
}
hr {
  /* width: 100%; */
  background-color: var(--secondary);
  height: 4px;
}

.Homeimg {
  width: 100%;
  opacity: 0.96;
  margin-bottom: 200px;
  /* object-fit: cover; */
}
.Homediv img {
  /* width: 100%; */
  /* height: 800px ; */
}
.Merch {
  height: 800px;
}

body {
  background-color: var(--primary);
  /* background-color: var(--lovelypink); */
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media screen and (max-width: 769px) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
  .Homeimg {
    width: 100%;
    position: relative;
    border: 2px solid black;
    overflow: hidden;
    margin-bottom: 200px;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
