*{
    /* border: 1px solid yellowgreen; */
   

}
:root{
    --primary:#25092c;
    --secondary: #9be8a1;
  }

body{
    display:inline;
    text-align: center;
} 
.Holder{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    /* height: fit-content; */

}

.imagediv img{
    position: relative;
    right: 15%;
    scale: 50%;
    border: 10px solid var(--secondary);
    border-radius: 20px;
    transition: all 1s ease;
    
}
.imagediv img:hover{
transform: rotate(360deg);
cursor: pointer;
}

.txtdiv{
    position: relative;
    right: 15%;
    
    /* border: 1px solid var(--secondary); */
  
    transition: all 1s ease;
    padding:5px;
    gap: 10px;
    display: flex;
    flex-direction: column;
    /* justify-content:space-evenly; */
    align-items: center;
    color: #9be8a1;
    width: 50%;
    height:inherit;
    /* flex-wrap: wrap; */
    
}
.txtdiv pre{
    display: inline-flex;
    flex-wrap: wrap;
}



.--btn{
    width:max-content;
    height: max-content;
    /* height: 3%; */
    color: var(--primary);
    /* background-color: var(--secondary); */
    font-size: 30px;
    font-weight: 900;
    padding: 8px;
    opacity: 1;
    cursor: pointer;
    z-index: 111;
    position: relative;
    right: -35%;
    top: 50px;
    border-radius: 10px;
    transition: all 0.5s ease-in-out;
    font-size: 30px;
    background: linear-gradient(270deg,#25092c,#9be8a1);

}

.--btn:hover{
    padding:10px;
    font-size: 35px;
}


@media screen and (max-width:650px){
    
    .Holder{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        
    }
    
    .txtdiv{
        color: #9be8a1;
        right: 0%;
        padding:5px;
        gap: 10px;
        display: flex;
        flex-direction: column;
        justify-content:space-evenly;
        
        
        width: fit-content;
        height:fit-content;
        
    }
    
    
    
}