*{margin: 0;
    
}

body{
    color: var(--secondary);
    
}

form{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
}
input{
    background-color: var(--secondary);
    color: var(--primary);
    border-radius: 3px;
}

.keywords{
    border: 2px solid var(--secondary);
    background-color: var(--secondary);
    color: var(--primary);
    padding: .5em;
    border-radius: 3px;
    /* margin-top: 1em; */
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: .25em;


}

.keywords input{
    background-color: var(--secondary);
    flex-grow: 1;
    padding: .5em 0;
    border: none;
    outline: none;

    
}
.errors{
    color: red;
    font-size: x-small;
}

/* p{
    color: red;
} */

.tag-item{
    background-color: var(--secondary);
    display: inline-block;
    padding: .25em .25em;
}
.tag-item span.close{
    height: 20px;
    width: 20px;
    background-color: var(--primary);
    color: var(--secondary);
    border-radius: 50%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-left: .25em;
    font-size: 18px;
    cursor: pointer;
}